<template>
  <div class="remd">
    <div class="content">
      <title-Bar :text="title1" />
      <div class="remdBox">
        <album  v-for="item  in albumList" :albumIfo="item" :key="item.name" />
      </div>
      <title-Bar :text="title2" />
      <div class="newBox">
        <song-item v-for=" value in newList" :key="value.id" :newList="value"/>
      </div>
    </div>
     
      
  </div>
</template>

<script>
// 组件
import titleBar from '@/components/content/remd/Titlebar'
import album from '@/components/content/remd/Album'
import songItem from '@/components/content/remd/songItem'

// 网络请求
import {requestNew,requestRemd} from '@/network'

// better-scroll
import {scroll} from '@/scroll'


export default {
  name:"remd",
  data(){
    return {
      title1:"推荐歌单",
      title2:"最新音乐",
      albumList:[],
      newList: [],
      scroller:''
    }
  },
  components:{
    titleBar,
    album,
    songItem
  },
  created(){
    //获取推荐歌单
    requestRemd().then(res=>{
      this.albumList = res.data.result;
    })

    // 获取最新音乐
    requestNew().then(res=>{
      this.newList = res.data.result;
    })
  },
  mounted(){
     // better-scroll
      this.scroller = scroll('.remd')
    
  }
}
</script>

<style scoped>
  .remd{
    width: 100%;
    height: calc(100vh - 124px);
    overflow: hidden;

  }
  .remdBox{
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: flex-start;
    flex-wrap:  wrap;
  }
  
</style>