<template>
<div class="titleBar">
    <p>{{text}}</p>
</div>
</template>

<script>
export default {
name: 'titleBar',
props:{
    text:{
        type: String,
        default:''
    }
}

}
</script>

<style scoped>
.titleBar{
    width: 100%;
    height: 52px;
}
.titleBar::after{
    content: '';
    display: inline-block;
    height: 52px;
    width: 0;
    vertical-align: middle;
}
.titleBar p{
    font-size: 17px;
    border-left: 2px solid #d43c33;
    display: inline-block;
    padding-left: 8px;
}
</style>