<template>
  <div class="navBar">
      <ul>
          <li v-for="(item,index ) in navList" :key="item.text" :class="['item',{active:isActive(index)}]" @click="itemClick(item,index)">{{item.text}}</li>
      </ul>
  </div>
</template>

<script>
export default {
    name: 'navbar',
    props:{
        items:{
            type: Array,
            default(){
                return [];
            }
        },
        navList:{
            type: Array,
            default(){
                return [];
            }
        }
    },
    data(){
        return {
            activeItem: 0,
        }
    },
    methods:{
        itemClick(item,index){
            // 不跳转相同路由
            if(this.$router.history.current.fullPath !== item.router){
                this.$router.push(item.router);
                this.activeItem = index;
            }else{
                return;
            }
        },
        isActive(index){
            return index === this.activeItem
        }
    },
    computed:{
        
            
        
    }
}
</script>

<style scoped>
.navBar{
    font-size: 15px;
    color: inherit;
    height: 42px;
    background-color: white;
     display: flex;
    justify-content: space-evenly;
    align-items: center;
    border-bottom: 1px solid rgba(34,34,34,.1);
}
ul{
    padding: 0;
    margin: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
   
}
ul,li{
    list-style: none;
}
.item{
    height: 100%;
    line-height: 42px;
    padding: 0 5px;
    border-bottom: 2px solid transparent;
}
.active{
    color: #d43c33;
    border-bottom: 2px solid #d43c33;
}
</style>