import vueRouter from 'vue-router'
import vue from 'vue'

import remd from '@/views/Remd'
import hot from '@/views/Hot'
import search from '@/views/Search'

vue.use(vueRouter);

const router = new vueRouter({
    routes:[
        {
            path: '',
            redirect: '/remd'
        },
        {
            path: '/remd',
            component: remd
        },
        {
            path: '/hot',
            component: hot
        },
        {
            path: '/search',
            component: search
        }
    ],
    mode: 'hash',
});

export default router;

  