<template>
  <a href="#">
    <div class="album">
      <img :src="albumIfo.picUrl" class="albumPic">
        <p class="playCount">
            <i class="iconfont icon-erji"></i>
            {{albumIfo.playCount | playCount}}
        </p>
      <p class="desciption">{{albumIfo.name | isLong}}</p>
    </div>
  </a>
</template>

<script>
export default {
    name:"album",
    props:{
        albumIfo:{
            type: Object,
            default(){
                return {}
            }
        }
    },
    filters:{
        playCount(value){
            return (value/10000).toFixed(1) + '万';
        },
        isLong(value){
            let length = 20;
            let result = value;
            if(result.length > length){
                return result.substr(0,length)+'...'
               
            }else{
                return result;
            }
        }
    },

}
</script>

<style scoped>
    a{
        text-decoration: none;
        color: inherit;
        margin-bottom: 15px;
    }
    .album{
        width: 135px;
        position: relative;
    }
    .albumPic{
        width: 135px;
        height: 135px;
    }
    .icon-erji{
        font-size: 15px;
    }
    .playCount{
        color: white;
        font-size: 12px;
        position: absolute;
        right: 5px;
        top: 3px;
    }
    .desciption{
        font-size: 13px;
        padding: 0 4px;
        width: 100%;
        box-sizing: border-box;
        word-break:break-all;
    }
</style>